/* HERO SECTION */
.hero-section {
  position: relative;
  height: 90vh;
  overflow: hidden;
}

/* SLIDES */
.hero-slide {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transform: scale(1);
  transition: opacity 1.5s ease-in-out, transform 6s ease;
  z-index: 1;
}

.hero-slide.active {
  opacity: 1;
  transform: scale(1.08); /* Ken Burns effect */
}

/* OVERLAY */
.hero-overlay {
  position: relative;
  z-index: 3;
  height: 100%;
  background: linear-gradient(
    rgba(0, 0, 0, 0.55),
    rgba(0, 0, 0, 0.55)
  );
  display: flex;
  justify-content: center;
  align-items: center;
}

/* CONTENT */
.hero-content {
  text-align: center;
  color: #fff;
  max-width: 800px;
  padding: 0 20px;
}

.hero-content h1 {
  font-size: 3rem;
  text-transform: uppercase;
}

.hero-subtitle {
  margin-top: 12px;
  font-size: 1.2rem;
  opacity: 0.9;
}

/* BUTTONS */
.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 18px;
  margin-top: 25px;
}

.hero-btn {
  padding: 14px 36px;
  border-radius: 40px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  transition: all 0.35s ease;
}

.hero-btn.primary {
  background: #d32f2f;
  color: #fff;
}

.hero-btn.secondary {
  background: #fff;
  color: #d32f2f;
}

.hero-btn:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.4);
}

/* DOTS */
.hero-dots {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 30px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: #fff;
  transform: scale(1.4);
}

/* WHY CHOOSE */
.why-choose {
  padding: 70px 20px;
  text-align: center;
}

.why-choose h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.why-choose p {
  max-width: 720px;
  margin: 0 auto 40px;
  font-size: 1.1rem;
}
/* COUNTERS */
.counters {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
  margin-top: 40px;
}

.counter {
  text-align: center;
  position: relative;
  background: rgba(255, 255, 255, 0.05);
  padding: 30px 20px;
  border-radius: 20px;
  min-width: 150px;
  transition: transform 0.5s ease, box-shadow 0.5s ease;
  cursor: default;
}

.counter:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}

.counter h3 {
  font-size: 2.5rem;
  color: #1a4f8b; /* RGM Blue */
  margin: 0;
  position: relative;
  z-index: 1;
}

.counter p {
  font-size: 1.1rem;
  margin-top: 10px;
  color: #333;
  font-weight: 600;
}

/* GRAPHIC BARS */
.counter-bar {
  position: relative;
  width: 100%;
  height: 8px;
  background: rgba(26, 79, 139, 0.2);
  border-radius: 4px;
  margin-top: 15px;
  overflow: hidden;
}

.counter-bar-fill {
  height: 100%;
  width: 0;
  background: #1a4f8b;
  border-radius: 4px;
  transition: width 2s ease-in-out;
}

/* Add subtle glow animation */
.counter h3 {
  animation: glow 2s infinite alternate;
}

@keyframes glow {
  0% { text-shadow: 0 0 5px rgba(26,79,139,0.3); }
  100% { text-shadow: 0 0 20px rgba(26,79,139,0.8); }
}

/* Add a subtle fade-in from bottom when scrolling into view */
.counter.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: all 1s ease-out;
}

.counter.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}
/* Make "Why Choose RGM?" heading in company color */
.why-choose h2 {
  color: #1a4f8b;
}
