:root {
  --blue: #1a4f8b;
}

/* PAGE */
.rgm-truckload-page {
  max-width: 1200px;
  margin: auto;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
  color: #333;
  line-height: 1.6;
}

/* ================= HERO ================= */
.rgm-truckload-hero {
  position: relative;
  height: 80vh;
  min-height: 520px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 18px;
}

.hero-image-box {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.hero-image-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.55);
  animation: fade 1s ease;
}

.hero-content {
  position: relative;
  z-index: 2;
  background: rgba(0,0,0,0.55);
  padding: 60px 50px;
  border-radius: 20px;
  color: #fff;
  text-align: center;
}

.hero-content h1 {
  font-size: 46px;
  margin-bottom: 12px;
}

.hero-content p {
  font-size: 18px;
}

/* ================= INTRO ================= */
.rgm-truckload-intro {
  max-width: 900px;
  margin: 70px auto;
  text-align: center;
  font-size: 18px;
}

/* ================= MAIN SECTION ================= */
.rgm-truckload-main {
  text-align: center;
  margin: 80px 0;
}

.main-headline {
  font-size: 34px;
  color: var(--blue);
  margin-bottom: 10px;
  animation: fadeDown 0.8s ease;
}

.main-subtext span {
  display: inline-block;
  margin: 0 6px;
  font-weight: bold;
  animation: textPop 0.6s ease forwards;
}

.main-slideshow-frame {
  max-width: 900px;
  height: 480px;
  margin: 30px auto;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0 20px 45px rgba(0,0,0,0.25);
}

.main-slideshow-frame img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  animation: fade 1s ease;
}

/* Feature strip */
.main-features-strip {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 25px;
}

.feature-item {
  background: linear-gradient(135deg, var(--blue), #163f6b);
  color: #fff;
  padding: 12px 22px;
  border-radius: 30px;
  font-weight: bold;
  transition: transform 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-6px) scale(1.05);
}

/* ================= FEATURES ================= */
.rgm-truckload-features h2 {
  text-align: center;
  color: var(--blue);
  margin-bottom: 30px;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 22px;
}

.feature-card {
  background: #f5f9ff;
  padding: 28px;
  border-radius: 16px;
  text-align: center;
  font-weight: bold;
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-10px) scale(1.05);
}

/* ================= WHY ================= */
.rgm-truckload-why {
  text-align: center;
  margin: 70px 0;
}

.rgm-truckload-why h2 {
  color: var(--blue);
  margin-bottom: 15px;
}

/* ================= CTA ================= */
.rgm-truckload-cta {
  background: linear-gradient(135deg, var(--blue), #163f6b);
  color: #fff;
  padding: 70px 30px;
  border-radius: 18px;
  text-align: center;
}

.rgm-truckload-cta h2 {
  color: #fff;
  font-size: 36px;
  margin-bottom: 12px;
}

.cta-buttons {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.cta-primary,
.cta-secondary {
  padding: 14px 36px;
  border-radius: 30px;
  font-weight: bold;
  text-decoration: none;
  transition: transform 0.3s ease;
}

.cta-primary {
  background: #fff;
  color: var(--blue);
}

.cta-secondary {
  border: 2px solid #fff;
  color: #fff;
}

.cta-primary:hover,
.cta-secondary:hover {
  transform: scale(1.08);
}

/* ================= ANIMATIONS ================= */
@keyframes fade {
  from { opacity: 0.4; }
  to { opacity: 1; }
}

@keyframes fadeDown {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes textPop {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}
